

module.exports = function( el ) {
 	   
	var $el = $(el),
		$window = $(window);
	
		var yVal =  $('#barba-wrapper')[0].scrollHeight - $window.height() - $('footer').outerHeight() + 15;
	
	$('#barba-wrapper').on('scroll', function(event){
		//console.log($('#barba-wrapper').scrollTop())
		//console.log('yVal ' + yVal)
		if( $('#barba-wrapper').scrollTop() >= yVal ) {
			$el.addClass('no-sticky');
		} else {
			$el.removeClass('no-sticky');
		}
	});
		
		
};
  