/**
*	@desc module control for Product Items
*/

var settings = require('modules/settings');

module.exports = function( el ) {
	
	var $el = $(el),
		$window = $(window);
	
	var $pd = $('.product-description');
	var st = $('.scroll-container').scrollTop();
	
	//$('.product-description').bind('mousewheel', mouseWheel);
	var offset = 20; 
	var img_length = $el.find('img').length;
	
	function init(){
		$('.product-images-holder img').on('click', nextImage);
	}
	
	function nextImage(e){
		var img = $(e.currentTarget);
		var cont = $(img).parent();
		var total = $(img).parent().find('img').length - 1;
		var offset = $window.height() * .03;
		//console.log($(img).index());
		//console.log($(img).parent().find('img').length)
		
		if($(img).index() < total) {
			var next = $(img).index() + 1;
			//console.log($(cont).find('img').eq(next).position().top)
			$(settings.scrollContainer).animate({ scrollTop: $(cont).find('img').eq(next).position().top - offset }, 300);
		} else {
			$(settings.scrollContainer).animate({ scrollTop: 0 }, 300, 'linear');
		}
	}
	
	$el.on('mouseover', function(event){
		
		//console.log('image length ' + img_length) 	
		
		
		$el.on('mousemove', function(event){
			var el_x = $el.find('img').position().left + $el.find('img').width();
			//var el_y = $el.find('img').position().top + $el.find('img').height();
			var x = event.clientX;
			//var y = event.clientY;
			
			var x_pos =  el_x - x;
			var x_factor = $el.find('img').width() / img_length;
			
			var x_pos_alt = x - $el.find('img').position().left + offset;
			
			var select_image = 0;
			
			for(var i=0; i<img_length; i++) {
				
				if(x_pos_alt > i * x_factor) {
					select_image = i;
				}
				
				if( x_pos > i * x_factor ) {
					//select_image = i;
					
				}
			} 
			
			$el.find('a.grid__image').css('background-image', 'url('+$el.find('img').eq(select_image).attr('src')+')');
			//console.log(select_image);
			//$el.find('img').hide();
			//$el.find('img').eq(select_image).show();
			
			
			//console.log( el_x - x );
		});
		
		
	}).on('mouseout', function(event){
		$el.off('mousemove');
	});
	
	
	$window.on('resize', winResize);
	
	
	function winResize(){
		if($('body').hasClass('template-collection')) return true;
		
		var lr_margin = $('.product-single').css("marginLeft").replace('px', '');
		
		console.log('settings.isMobile ' + settings.isMobile);
		
		if($('.product-images-holder').outerWidth() + lr_margin >= $window.width()/2 && !settings.isMobile) {
			$('.product-description').css('width', $window.width() -  $('.product-images-holder').outerWidth() - lr_margin );
		} else {
			$('.product-description').css('width', '');
		}
		
		$('.product-description').addClass('visible');
	}
	
	init();
	winResize();
	
}; 
  