var settings = require( "modules/settings" ),
	throttle = require("modules/throttle");


 "use strict";



module.exports = function( el ) {
 	   
		//var win = window;
	   
	    $('.hamburger').on('click', toggleMobileMenu);
		$('#barba-wrapper').on('click', closeMobileMenu);
		
		$('.cart-count-holder').on('click', toggleAjaxCart);
		
		function toggleMobileMenu(){
			$('body').toggleClass('show-mobile-menu');
		}
		
		function closeMobileMenu(){
			$('body').removeClass('show-mobile-menu');
		}
		
		function toggleAjaxCart(e){
			e.preventDefault();
			if($('body').hasClass('js-drawer-open')){
				
				$('*').removeClass('js-drawer-open js-drawer-open-right')
			}
			
		}
};
  